import { Component, OnInit, Input } from '@angular/core';
import { slideInAnimation } from '../slide-in-animation';
import { PriceLoaderService } from '../../service/rest/price-loader.service';


import { Router } from '@angular/router';

import { ReservationCheckComponent } from '../reservation-check.component';
import { element } from 'protractor';
import { forEach } from '@angular/router/src/utils/collection';

@Component({
  selector: 'app-reservation-categories',
  templateUrl: './reservation-categories.component.html',
  styleUrls: ['./reservation-categories.component.css'],
  providers: [
    PriceLoaderService
  ],
  animations: [slideInAnimation],
  host: { '[@routerAnimation]': '' }
})
export class ReservationCategoriesComponent implements OnInit {

  totalPrice:string = "";
  displayPrice: boolean = false;
  nextVisible: boolean = true;
  categories: Array<any> = [];
  categoryCount: Array<any> = [
    {
      num: 0
    },
    {
      num: 0
    },
    {
      num: 0
    }
  ];

  constructor(
    private priceLoader: PriceLoaderService,
    private router: Router, 
    private parent: ReservationCheckComponent) { }

  ngOnInit() {
    this.categories = this.parent.categories;
    if(this.parent.alertVisible == true){
      this.nextVisible = false;
    }
  }

  next(){
    this.router.navigate(
      [
      'reservation-check', 
      {
          outlets: {
            'reservation-personaldata-aux': ['reservation-personaldata']
          } 
      },
    ],
      {skipLocationChange: true}
    );
    this.parent.personaldataVisible = true;
    this.parent.categoryCount = this.categoryCount;
    this.priceLoader.sendRequest(this.categories, this.categoryCount, this.parent.startDate, this.parent.endDate).subscribe(
      data => {
        this.totalPrice = data["price"];
        this.displayPrice = true;
      }
    );
  }

  trackCategory(index, category){
    return category ? category.categoryId : undefined;
  }

  increment(index){
    console.log(this.categories[index].freeRooms);
    if(this.categoryCount[index].num < this.categories[index].freeRooms){
      this.categoryCount[index].num += 1;
    }
  }
  decrement(index){
    this.categoryCount[index].num > 0 ? this.categoryCount[index].num -= 1 : this.categoryCount[index].num = 0;
  }

}
