import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { HttpHeaders } from '@angular/common/http';
import { ReservationRequest } from '../../service/rest/model/reservation-request';
import { CategoryRequestProperties } from './category-request-properties';

@Injectable({
  providedIn: 'root'
})
export class ReservationRequestService {

  constructor(private httpClient: HttpClient) { }

  url = 'http://localhost:8080/DoReservation';
  httpOptions = {
    headers: new HttpHeaders({
      'Content-Type':  'application/json',
      'Accept': 'application/octet-stream',
      
    })
  };

  sendRequest(categories: Array<any>, categoryCount: Array<any>, request: ReservationRequest, startDate: string, endDate: string){
    categoryCount.forEach(function(element, i){
      if(element.num > 0){
        const part: CategoryRequestProperties = {
          categoryID: categories[i].categoryId.toString(),
          amount: element.num.toString(),
          startDate: startDate,
          endDate: endDate
        }
        request.categoryRequests.push(part);
      }
    });
    return this.httpClient.post(this.url, request,  {responseType: 'blob'});
  }

}
