import { Component, OnInit } from '@angular/core';
import { Router } from '@angular/router';
import { ReservationRequestService } from '../../service/rest/reservation-request.service';
import { ReservationRequest } from '../../service/rest/model/reservation-request';
import { ReservationCheckComponent } from '../reservation-check.component';
import { error } from 'protractor';

@Component({
  selector: 'app-reservation-personaldata',
  templateUrl: './reservation-personaldata.component.html',
  styleUrls: ['./reservation-personaldata.component.css']
})
export class ReservationPersonaldataComponent implements OnInit {

  constructor(private reservationService: ReservationRequestService,
  private parent: ReservationCheckComponent, private router: Router) { }

  firstname: string = "";
  lastname: string = "";
  eMail: string = "";
  location: string = "";
  street: string = "";
  houseNumber: string = "";
  creditCard: string = "";
  phoneNumber: string = "";
  country: string = "";
  postcode: string = "";

  ngOnInit() {
  }

  next(){
    let personalData: ReservationRequest = {
      fname: this.firstname,
      lname: this.lastname,
      eMail: this.eMail,
      place: this.location,
      street: this.street,
      housenumber: this.houseNumber,
      creditcard: this.creditCard,
      categoryRequests: [],
      country: this.country,
      postCode: this.postcode,
      phoneNumber: this.phoneNumber,
      getStartDate:this.parent.startDate,
      getEndDate: this.parent.endDate
    };
    this.reservationService.sendRequest(
      this.parent.categories, this.parent.categoryCount, personalData, 
      this.parent.startDate, this.parent.endDate
    ).subscribe(
      (data:any) => {
        let pdf = new Blob([data], {
          type: 'application/pdf'
        });
        /*
          Some javascript to download the pdf file
        */
        var url = window.URL.createObjectURL(pdf);
        var a = document.createElement('a');
        document.body.appendChild(a);
        a.setAttribute('style', 'display: none');
        a.href = url;
        a.download = "ReservationInfo";
        a.click();
        window.URL.revokeObjectURL(url);
        a.remove();

        this.router.navigateByUrl("/success");

      },
      error => {
        this.parent.errorMessage = "Something went wrong";
        this.parent.alertVisible = true;
      }
    );
  }


}
