import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { HttpHeaders } from '@angular/common/http';
import { CategoryRequestProperties } from './category-request-properties';

@Injectable({
  providedIn: 'root'
})
export class PriceLoaderService {

  url = 'http://localhost:8080/ReservationPrice';
  static requestData: Array<CategoryRequestProperties> = [];
 
  httpOptions = {
    headers: new HttpHeaders({
      'Content-Type':  'application/json'
    })
  };

  constructor(private httpClient: HttpClient) { }

  sendRequest(categories: Array<any>, categoryCount: Array<any>, startDate: string, endDate: string){
    console.log(startDate);
    categoryCount.forEach(function(element, i){
      if(element.num > 0){
        const part: CategoryRequestProperties = {
          categoryID: categories[i].categoryId.toString(),
          amount: element.num.toString(),
          startDate: startDate,
          endDate: endDate
        }
        PriceLoaderService.requestData.push(part);
      }
    });
    return this.httpClient.post(this.url, PriceLoaderService.requestData, this.httpOptions);
  }
}
