import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { HttpHeaders } from '@angular/common/http';
import { CheckProperties } from './check-properties';

@Injectable({
  providedIn: 'root'
})
export class ReservationCheckService {

  url = 'http://localhost:8080/ReservationCheck';
  requestData: CheckProperties;
 
  httpOptions = {
    headers: new HttpHeaders({
      'Content-Type':  'application/json'
    })
  };

  constructor(private httpClient: HttpClient) {
    
  }

  sendRequest(startDate: string, endDate: string){
      this.requestData = {
        startDate: startDate,
        endDate: endDate
      };
      return this.httpClient.post<any[]>(this.url, this.requestData, this.httpOptions);
  }
}
