import {
    trigger,
    state,
    style,
    animate,
    transition,
    query,
    group,
  } from '@angular/animations';

  export const slideInAnimation = 
    trigger('routerAnimation', [
        transition('* <=> *', [
          query(
            ':enter, :leave', 
            style({
               
            }),
            { optional: true }
          ),
          group([
            query(':leave', [
              style({ opacity: 1 }),
              animate('3s ease-in-out', style({ opacity: 0 })),
             ], { optional: true }
            ),
            query(':enter', [
              style({ opacity: 0 }),
              animate('3s ease-in-out', style({ opacity: 1 }))
            ], { optional: true })
        ]),

      ]
    )]);