import { BrowserModule } from '@angular/platform-browser';
import { NgModule } from '@angular/core';
import { FormsModule } from '@angular/forms';
import { BrowserAnimationsModule } from '@angular/platform-browser/animations';
import { RouterModule, Routes } from '@angular/router';
import { HttpClientModule } from '@angular/common/http';

import { AppComponent } from './app.component';
import { NavComponent } from './nav/nav.component';
import { ReservationCheckComponent } from './reservation-check/reservation-check.component';
import { OverviewComponent } from './overview/overview.component';
import { ReservationDatepickerComponent } from './reservation-check/reservation-datepicker/reservation-datepicker.component';
import { ReservationCategoriesComponent } from './reservation-check/reservation-categories/reservation-categories.component';
import { ReservationPersonaldataComponent } from './reservation-check/reservation-personaldata/reservation-personaldata.component';

import { OutletWrapper } from './outlet-wrapper';
import { SuccessComponent } from './success/success.component';

const appRoutes: Routes = [
  {
    path: '',
    redirectTo: 'overview',
    pathMatch: 'full'
  },
  { 
    path: 'reservation-check', 
    component: ReservationCheckComponent,
    children: [
      {
          path: '',
          redirectTo: '/reservation-check/(reservation-datepicker-aux:reservation-datepicker)',
          pathMatch: 'full'
      },
      {
        path: 'reservation-datepicker',
        component: ReservationDatepickerComponent,
        data: {
          animation: 'reservation-datepicker'
        },
        outlet: 'reservation-datepicker-aux'
      },
      {
        path: 'reservation-categories',
        component: ReservationCategoriesComponent,
        data: {
          animation: 'reservation-categories'
        },
        outlet: 'reservation-categories-aux'
      },
      {
        path: 'reservation-personaldata',
        component: ReservationPersonaldataComponent,
        data: {
          animation: 'reservation-personaldata'
        },
        outlet: 'reservation-personaldata-aux'
      }, // ECHT JETZT? wenn hier kein komma ist, funktioniert das routing nicht... 
    ]
  },
  {
    path: 'overview',
    component: OverviewComponent
  },
  {
    path: 'success',
    component: SuccessComponent
  }
];

@NgModule({
  declarations: [
    AppComponent,
    ReservationCheckComponent,
    OverviewComponent,
    NavComponent,
    ReservationDatepickerComponent,
    ReservationCategoriesComponent,
    ReservationPersonaldataComponent,
    OutletWrapper,
    SuccessComponent
  ],
  imports: [
    BrowserModule,
    FormsModule,
    HttpClientModule,
    BrowserAnimationsModule,
    RouterModule.forRoot(
      appRoutes,
      { enableTracing: true } // for debugging
    )
  ],
  providers: [],
  bootstrap: [AppComponent]
})
export class AppModule { }
