import { Component, OnInit, Input } from '@angular/core';
import { Router } from '@angular/router';
import { slideInAnimation } from '../slide-in-animation';
import { ReservationCheckService } from '../../service/rest/reservation-check.service';
import { DateValidationService } from '../../service/validation/date-validation.service';
import { OutletWrapper } from '../../outlet-wrapper';
import { ReservationCheckComponent } from '../reservation-check.component';
import { Observable } from 'rxjs';

@Component({
  selector: 'app-reservation-datepicker',
  templateUrl: './reservation-datepicker.component.html',
  styleUrls: ['./reservation-datepicker.component.css'],
  providers: [ReservationCheckService],
  animations: [slideInAnimation],
  host: { '[@routerAnimation]': '' }
})
export class ReservationDatepickerComponent implements OnInit {

  startDate: string;
  endDate: string; 

  inputEnabled: boolean = true;

  wrapUpEvent = new CustomEvent('wrapUp');

  @Input() datepickerVisible: boolean;
  
  constructor(
    private reservationCheckService: ReservationCheckService, 
    private router: Router, 
    private dateValidation: DateValidationService,
    private parent: ReservationCheckComponent) {
      parent.datepickerVisible = true;
    }
  
    
  
    ngOnInit() {
      this.datepickerVisible = true;
    }

  checkReservation(){
    this.reservationCheckService.sendRequest(this.startDate, this.endDate).subscribe(
        data => {
          let i = 0;
          data.forEach(element =>{
            i += 1;
            this.parent.categories.push(element);
          })
          if(i == 0){
            this.parent.errorMessage = "Sorry, there are no more rooms available";
            this.parent.alertVisible = true;
          }
        }
    );
  }

  dateCheck(){
    if(this.dateValidation.validate(this.startDate, this.endDate)){
      this.inputEnabled = false;
      this.parent.startDate = this.startDate;
      this.parent.endDate = this.endDate;
      this.checkReservation();
      this.next();
    }
  }

  next(){
    this.router.navigate([
      '/reservation-check',
      { 
      outlets: {
        'reservation-categories-aux': ['reservation-categories']
      } },],
      {skipLocationChange: true}
    );
    this.parent.categoryVisible = true;
  }

}
